
import React, { useRef, useState , useContext} from "react"
import { Form, Button, Card, Alert, Container } from "react-bootstrap"
import { loggedUser,updatePassword, updateEmail} from  "../components/FirebaseAuth"
import { Link, navigate } from "gatsby"
import { PageLayout } from "../components"
import { SEO } from "../utils"

export default function UpdateProfile() 
{
  const emailRef = useRef()
  const passwordRef = useRef() 
  const passwordConfirmRef = useRef()
  const currentUser = loggedUser()
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)


  function handleSubmit(e) 
  {
    e.preventDefault()
    if (passwordRef.current.value !== passwordConfirmRef.current.value) 
    {
      return setError("Passwords do not match")
    }
    
    const promises = []
    setLoading(true)
    setError("")

    if (emailRef.current.value !== currentUser.email) 
    {
      promises.push(updateEmail(emailRef.current.value))
    }
    if (passwordRef.current.value) 
    {
      promises.push(updatePassword(passwordRef.current.value))
    }

    Promise.all(promises)
      .then(() => 
      {
        navigate(`/Dashboard`)
      })
      .catch((e) => 
      {
        setError(`Failed to update account ${e && e.message}`)
      })
      .finally(() => 
      {
        setLoading(false)
      })
  }

  return (
    <>
    <PageLayout>
      <SEO title="Sign Up" />
      <Container className="d-flex align-items-center justify-content-center" >
        <div className="w-100 text-color" style={{ maxWidth: "400px" }}>
          <Card style={{ borderColor:"grey",borderWidth:"3px" }}>
            <Card.Body>

              <h2 className="text-center mb-4">Update Profile</h2>
              {error && <Alert variant="danger">{error}</Alert>}

              <Form onSubmit={handleSubmit}>
                <Form.Group id="email">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    ref={emailRef}
                    required
                    defaultValue={currentUser.email}
                  />
                </Form.Group>
                <Form.Group id="password">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    type="password"
                    ref={passwordRef}
                    placeholder="Leave blank to keep the same"
                  />
                </Form.Group>
                <Form.Group id="password-confirm">
                  <Form.Label>Password Confirmation</Form.Label>
                  <Form.Control
                    type="password"
                    ref={passwordConfirmRef}
                    placeholder="Leave blank to keep the same"
                  />
                </Form.Group>
                <Button disabled={loading} className="w-100" type="submit">
                  Update
                </Button>
              </Form>

            </Card.Body>
          </Card>

          <div className="w-100 text-center mt-2">
            <Link to="/">Cancel</Link>
          </div>
        </div>
      </Container>
    </PageLayout>
    </>
  )
}